import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	computed: {
		...mapState(['splashAdvertPreTime']),
	},
	methods: {
		...mapMutations(['login'])
	},
	onLaunch: function() {
		//重置登陆状态
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				this.login(res.data);
			}
		});			
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
}
